@import url(https://fonts.googleapis.com/css2?family=Sofia&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  direction: rtl;
  text-align: right;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.AppMain{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.AppMain .custom-control.custom-switch{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.AppMain .jumbotron{
  background-color: rgb(235, 170, 193);
  margin: 5px 5px;
  margin-bottom: 10px;
  padding: 0;
}

.AppMain .jumbotron h1{
  font-family: 'Sofia', cursive;
  font-size: 64px;
  line-height: normal;
  color: white;
  margin: 0;
  padding: 15px;
  text-align: center;
}

.navbar.navbar-expand-lg.navbar-light{
    background-color: rgb(255, 227, 231);
    color:white;
    position: relative;
  }

.navbar.navbar-expand-lg.navbar-light:hover{
    background-color: rgb(253, 205, 213);
  }

.navbar.navbar-expand-lg.navbar-light a{
    color: black;
}

.navbar.navbar-expand-lg.navbar-light a:hover{
    color:white;
}

.navbar.navbar-expand-lg.navbar-light .btn{
    color: black;
    border-color: black;
    margin: 0 5px;
}

.navbar.navbar-expand-lg.navbar-light .btn:hover{
    color: white;
    border-color: white;
    background-color: rgb(253, 179, 191);

}

.navbar.navbar-expand-lg.navbar-light .show{
    z-index: 10;
    box-shadow: 2px 10px 9px -3px rgba(112,108,112,0.5);
    position: absolute;
    top: 56px;
    right: 0;
    padding: 25px;
    background-color: rgb(253, 235, 238);
    color:white;
    width: 100%;
}

.navbar.navbar-expand-lg.navbar-light .show:hover{
    background-color: rgb(255, 227, 231);
  }

.navbar.navbar-expand-lg.navbar-light .show a{
    color: black;
    margin: 5px 0;
}

.navbar.navbar-expand-lg.navbar-light .show input{
    margin-left: 10px;
    width: 50vw;
    display: inline;
}

.navbar.navbar-expand-lg.navbar-light .show a:hover{
    color:white;
}

.navbar.navbar-expand-lg.navbar-light .show .btn{
    display: inline;
    color: black;
    border-color: black;
    margin: 0;
    margin-top: 5px;
}

.navbar.navbar-expand-lg.navbar-light .show .btn:hover{
    color: white;
    border-color: white;
    background-color: rgb(253, 179, 191);
}

.navbar.navbar-expand-lg.navbar-light .collapsing{
    display: none;
}

.h-pg .container{
    background-color: rgb(235, 170, 193);
    padding: 0;
}




.profile-pg .container form{
    width: 50%;
    margin: auto;
    padding: 15px;
}


.profile-pg .container .form-group{
    width: auto;
}

.profile-pg .btn{
    width: 100%;
    background-color: rgb(78, 151, 78);
    color: white;
}

.profile-pg .btn:hover{
    width: 100%;
    color: rgb(78, 151, 78);
    background-color: white;
}

/* On screens that are 992px wide or less, go from four columns to two columns */
@media screen and (max-width: 770px) {
    .profile-pg .container form{
        width: 75%;
        margin: auto;
        padding: 15px;
    }
    
}
.calendar-c .custom-select{
  width: auto;
  box-shadow: 2px 10px 9px -3px rgba(112,108,112,0.5);

}

.calendar-c .month {
  color: var(--color);
  grid-template-columns: repeat(7, 1fr);
  z-index: 2;
  background-color: white;
  display: grid;
  width: auto;
  height: 50vh;
}

.calendar-c .weekday,
.calendar-c .day {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 7px;
  cursor: pointer;
  border: gainsboro solid;
  border-width: 1px;
}

.calendar-c .day:hover {
  background-color: pink;
}

.calendar-c .p{
  background-color: yellow;
}

.calendar-c .bthDay {

  background-color: palevioletred;
}

.calendar-c .m{
  background-color: rgb(83, 114, 173);
}

.calendar-c .holiday{
  background-color: darkorange;
}

.calendar-c .v{
  background-color: crimson;
}

.calendar-c .h{
  background-color: lightgreen;
}

.calendar-c .other{
  background-color: darkgrey;
}

.calendar-c .weekday {
  color: #000;
  font-weight: bold;
  border: gainsboro solid;
  text-align: center;
  display: flex;
  justify-content: space-around;
}

.calendar-c .pastMonth{
  color: gray;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 7px;
  cursor: pointer;
}

/* On screens that are 992px wide or less, go from four columns to two columns */
@media screen and (max-width: 770px) {
  .calendar-c .month{
    height: 25px;
  }
  .calendar-c .day{
    display: flex;
    padding: 0;
    padding-right: 5px;
    flex-direction: column;
    justify-content: space-evenly;
  }
}

